import React from 'react'
import CardItem from './CardItem'
import './Cards.css'

function Cards() {
    return (
        <div className='cards'>
            <h1>Title for the Cards</h1>
            <div className="cards__container">
                <div className="cards__wrapper">
                    <ul className="cards__items">
                        <CardItem 
                            src="images/image-1.jpg"
                            text="This first image is the frosted image over a hill range"
                            label='Foggy Hill'
                            path='/games/books'
                        />
                        <CardItem 
                            src="images/image-2.png"
                            text="This second image is the frosted image over a hill range"
                            label='BW photo'
                            path='/games/movies'
                        />                    
                    </ul>

                    {/* new ul creates a new line of cards */}
                    <ul className="cards__items">
                        <CardItem 
                            src="images/image-1.jpg"
                            text="This third image is the frosted image over a hill range"
                            label='Foggy Hill'
                            path='/games/series'
                        />
                        <CardItem 
                            src="images/image-2.png"
                            text="This fourth image is the frosted image over a hill range"
                            label='Bw'
                            path='/games/about'
                        />
                        <CardItem 
                            src="images/image-1.jpg"
                            text="This fourth image is the frosted image over a hill range"
                            label='Bw'
                            path='/games/about'
                        />
                    </ul>




                </div>
            </div>
        </div>
    )
}

export default Cards
