import React from 'react'
import './Button.css';
import { Link } from 'react-router-dom';


// array for selecting styles of the button
const STYLES = ['btn--primary', 'btn--outline'];

// array for sizes of the button
const SIZES = ['btn--medium', 'btn--large'];

export const Button = ({children, type, onClick, buttonStyle, buttonSize}) => {

    // checks if the button style is correct, autostyles to primary
    const checkButtonStyle = STYLES.includes(buttonStyle) ? buttonStyle : STYLES[0];

    //  checks if the button sized correctly, autosizesto medium
    const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

    return (
        <Link to='/games/about/' className='btn-mobile'>

            {/* creates a button when automatically checks style and size. Characteristics onClick and is of type declared */}
            <button className={`btn ${checkButtonStyle} ${checkButtonSize}`} onclick={onClick} type={type}>
                {/* whatever the variable children is will be displayed */}
                {children}
            </button>
        </Link>
    )
}