import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom';
import { FaTimes, FaBars, FaTypo3} from 'react-icons/fa';
import { Button } from './Button';
import './Navbar.css';
// var Link = require('react-router-dom').Link



function Navbar() {

    // function used to update the state of the 
    const [click , setClick] = useState(false);
    // create button 
    const [button, setButton] = useState(true);

    // handleclick is an arrow function which reverses
    const handleClick = () => setClick(!click);
    // causes the menu to close by setting click to false
    const closeMobileMenu = () => setClick(false);

    // display button dependent on whether mobile or full screen
    const showButton = () => {
        if(window.innerWidth <= 960){
            setButton(false);
        } else {
            setButton(true);
        }
    }

    // when refresshing, the signup button show up in the middle of the screen
    // this is a hack way of getting rid of that problem
    useEffect(() => {
        showButton();
    })

    // create listener of the window to determine whether button shows up
    // every time the window is resizes, runs the function showButton
    window.addEventListener('resize', showButton)

    return (
        <>

            <nav className="navbar">
                <div className="navbar-container">

                    {/* link does the same think as "a" in html */}
                    <Link to="/games/" className="navbar-logo" onClick={closeMobileMenu}>
                        games <FaTypo3 className="fa-typo3"/>
                    </Link>

                    <div className="menu-icon" onClick={handleClick}>
                        {/* if click is false, fas fa-times (cross icon) else fas fa-bars (burger icon) */}
                        {click ? <FaTimes className="fa-times"/> : <FaBars className="fa-bars"/>}
                    </div>

                    {/* Create a closable menu  */}
                    <ul className={click ? "nav-menu active" : "nav-menu"}>

                        {/* menu links to home */}
                        <li className="nav-item">
                            <Link to='/games/' className='nav-links' onClick={closeMobileMenu}>
                                Home
                            </Link>
                        </li>

                        {/* menu links to Books */}
                        <li className="nav-item">
                            <Link to='/games/books/' className='nav-links' onClick={closeMobileMenu}>
                                Books
                            </Link>
                        </li>

                        {/* menu links to movies */}
                        <li className="nav-item">
                            <Link to='/games/movies/' className='nav-links' onClick={closeMobileMenu}>
                                Movies
                            </Link>
                        </li>

                        {/* menu links to series */}
                        <li className="nav-item">
                            <Link to='/games/series/' className='nav-links' onClick={closeMobileMenu}>
                                Series
                            </Link>
                        </li>

                        {/* menu links to about */}
                        <li className="nav-item">
                            <Link to='/games/about/' className='nav-links-mobile' onClick={closeMobileMenu}>
                                About
                            </Link>
                        </li>
                    </ul>

                    {/* implements the button, button component includes a function which makes it only show when the screen is wide enough*/}
                    {button && <Button buttonStyle='btn--outline'>SIGN UP</Button>}

                </div>                
            </nav>


        </>
    )
}

export default Navbar
