import React from 'react';
import Navbar from './components/Navbar.js';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Home from './components/pages/Home'
import About from './components/pages/about';
import Series from './components/pages/series';
import Books from './components/pages/books';
import Movies from './components/pages/movies';


function App() {
  return (
    <>

    {/* Router contains all of the routes for the pages/paths */}
    <Router>
      <Navbar />
      
      <Switch>

        {/* exact will send you to home */}
        <Route path='/games/' exact component={Home}/>
        <Route path='/games/about/'  component={About}/>
        <Route path='/games/series/'  component={Series}/>
        <Route path='/games/books/'  component={Books}/>
        <Route path='/games/movies/'  component={Movies}/>
      </Switch>

    </Router>
    </>
  );
}

export default App;
