import React from 'react'
import { Link } from 'react-router-dom'

function CardItem(props) {
    return (
        <>
            {/* Define the output of what a card is */}
            <li className="cards__item">

                {/* Creates a card which contain a link to the given path/destination */}
                <Link className="cards__item__link" to={props.path}>

                    {/* include an image src with the given label and save label as data-category */}
                    <figure className="cards__item__pic-wrap" data-category={props.label}>
                        <img src={props.src} alt="Travel Image" className="cards__item__img" />
                    </figure>

                    {/* include a caption for the image provided by given text */}
                    <div className="cards__item__info">
                        <h5 className="cards__item__text">
                            {props.text}
                        </h5>
                    </div>
                    
                </Link>
            </li>
        </>
    )
}


export default CardItem
