import React from 'react';
import '../App.css'
import { Button } from './Button';
import './HeroSection.css'
import { AiOutlinePlayCircle } from 'react-icons/ai';

function HeroSection() {
    return (
        <div className='hero-container'>
            {/* because gh-pages is on a different branch, save videos to public folder. you also do not need a / before videos for the folder as already in base directory */}

            {/* <video src='videos/video-2.mp4' autoPlay loop muted /> */}
            <video src='videos/video-1.mp4' autoPlay loop muted />

            <h1>WELCOME TO THE ARTICLE</h1>
            <p>Where you can read the latest about stuff</p>
            <div className='hero-btns'>
                <Button className='btns' buttonStyle='btn--outline' buttonSize='btn--large'>
                    GET STARTED
                </Button>
                <Button className='btns' buttonStyle='btn--primary' buttonSize='btn--large'>
                    Watch This <AiOutlinePlayCircle className="aiplay"/>
                </Button>
            </div>
        </div>
    )
}

export default HeroSection
