import React from 'react'
import {Button} from './Button'
import './Footer.css'
import {Link} from 'react-router-dom'
import { FaTypo3} from 'react-icons/fa';
import { AiOutlineFacebook, AiOutlineGithub, AiOutlineInstagram} from 'react-icons/ai';



function Footer() {
    return (
        <div className='footer-container'>
            <section className='footer-subscription'>
                <p className='footer-subscription-heading'>
                    Join the Article newsletter to receive the newest games
                </p>
                <p className='footer-subscription-text'>
                    You can unsubscribe at any time.
                </p>
                <div className='input-areas'>
                    <form>
                        <input 
                            type='email' 
                            name='email' 
                            placeholder='Your Email' 
                            className='footer-input' 
                        />
                        <Button buttonStyle='btn--outline'>Subscribe</Button>
                    </form>
                </div>
            </section>

            <div class='footer-links'>
                <div className='footer-link-wrapper'>
                    <div class='footer-link-items'>
                        <h2>About Us</h2>
                        <Link to='/games/'>How it works</Link>
                        <Link to='/games/'>Testimonials</Link>
                        <Link to='/games/'>Careers</Link>
                        <Link to='/games/'>Investors</Link>
                        <Link to='/games/'>Terms of Service</Link>
                    </div>
                    <div class='footer-link-items'>
                        <h2>Contact Us</h2>
                        <Link to='/games/'>Contact</Link>
                        <Link to='/games/'>Support</Link>
                        <Link to='/games/'>Destinations</Link>
                        <Link to='/games/'>Sponsorships</Link>
                    </div>
                </div>
                <div className='footer-link-wrapper'>
                    <div class='footer-link-items'>
                        <h2>Videos</h2>
                        <Link to='/games/'>Submit Video</Link>
                        <Link to='/games/'>Ambassadors</Link>
                        <Link to='/games/'>Agency</Link>
                        <Link to='/games/'>Influencer</Link>
                    </div>
                    <div class='footer-link-items'>
                        <h2>Social Media</h2>
                        <Link to='/games/'>Instagram</Link>
                        <Link to='/games/'>Facebook</Link>
                        <Link to='/games/'>Youtube</Link>
                        <Link to='/games/'>Twitter</Link>
                    </div>
                </div>
            </div>
            
            <section class='social-media'>
                <div class='social-media-wrap'>
                    <div class='footer-logo'>
                        <Link to='/games/' className='social-logo'>
                        Article <FaTypo3 className="fa-typo3"/>
                        </Link>
                    </div>
                    <small class='website-rights'>Article © 2021</small>
                <div class='social-icons'>
                    <Link
                        class='social-icon-link facebook'
                        to='/games/'
                        target='_blank'
                        aria-label='Facebook'
                    >
                        <AiOutlineFacebook/>
                        <i class='fab fa-facebook-f' />
                    </Link>
                    <Link
                        class='social-icon-link instagram'
                        to={{pathname: 'https://github.com/NatZha'}}
                        rel="noreferrer"
                        target='_blank'
                        aria-label='Instagram'
                    >
                        <AiOutlineInstagram />
                    </Link>

                    <a class='social-icon-link github' rel="noreferrer" target="_blank" href="https://github.com/NatZha" aria-label='Github'><AiOutlineGithub/></a>

                </div>
                </div>
            </section>
        </div>
    )
}

export default Footer
